import React from "react";

import Button from "@mui/material/Button";
import { styled } from "@mui/material/styles";
import PropTypes from "prop-types";

const CMCurveButton = (props) => {
  const { color, label, startIcon, children, sx, ...rest } = props;

  const CustomButton = styled(Button)(({ other, theme }) => ({
    borderRadius: "0 0 0 17px",
    fontSize: "16px",
    padding: "12px 20px",
    backgroundColor: `${theme.palette[other.color].main}`,
    position: "relative",
    zIndex: "1",
    overflow: "hidden",
    "&::before": {
      content: `""`,
      position: "absolute",
      left: "0",
      top: "0",
      width: "100%",
      height: "100%",
      zIndex: "1",
      backgroundColor: `${
        other.color === "primary"
          ? theme.palette.secondary.main
          : theme.palette.primary.main
      }`,
      transform: "scaleX(0)",
      transition: "all .5s cubic-bezier(0.5, 1.6, 0.4, 0.7)",
    },
    "&:hover::before,&:active::before,&:focus::before": {
      transform: "scaleX(1)",
      zIndex: "-1",
      transition: "all .5s cubic-bezier(0.5, 1.6, 0.4, 0.7)",
    },
  }));
  
  return (
    <>
      <CustomButton
        variant="contained"
        color={color ? color : "primary"}
        sx={{ ...sx }}
        other={props}
        {...rest}
      >
        {label}
        {children}
      </CustomButton>
    </>
  );
};

CMCurveButton.propTypes = {
  label: PropTypes.string,
  children: PropTypes.any,
  color: PropTypes.string.isRequired,
};
export default CMCurveButton;
