import React from "react";

import Button from "@mui/material/Button";
import { alpha, styled } from "@mui/material/styles";
import PropTypes from "prop-types";

const CMAnimateButton = (props) => {
  const { color, label, startIcon, children, sx, ...rest } = props;
  const CustomButton = styled(Button)(({ other, theme }) => ({
    borderRadius: "50px",
    fontSize: "16px",
    padding: "6px 28px 6px 10px",
    boxShadow: "none",
    [theme.breakpoints.down("md")]: {
      fontSize: "14px",
      padding: "6px 18px 6px 10px",
    },
    "& .MuiButton-startIcon": {
      backgroundColor: theme.palette.primary.contrastText,
      color: `${theme.palette[other.color].main}`,
      width: "45px",
      height: "45px",
      borderRadius: "50px",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      marginRight: "20px",
      [theme.breakpoints.down("md")]: {
        width: "32px",
        height: "32px",
        marginRight: "12px",
      },
      "& .MuiSvgIcon-root": {
        fontSize: "20px",
        color: `${theme.palette[other.color].main}`,
        transition: "all 0.3s",
      },
    },

    "&:hover,&:active,&:focus": {
      boxShadow: `0 8px 16px 0 ${
        other.color === "primary"
          ? alpha(theme.palette.secondary.main, 0.3)
          : alpha(theme.palette.primary.main, 0.3)
      }`,
      backgroundColor:
        other.color === "primary"
          ? theme.palette.secondary.main
          : theme.palette.primary.main,
      "& .MuiButton-startIcon": {
        "& .MuiSvgIcon-root": {
          color:
            other.color === "primary"
              ? theme.palette.secondary.main
              : theme.palette.primary.main,
          transform: "rotate(90deg)",
          transition: "all 0.3s",
        },
      },
    },
  }));

  return (
    <>
      <CustomButton
        variant="contained"
        startIcon={startIcon}
        color={color ? color : "primary"}
        sx={{ ...sx }}
        other={props}
        {...rest}
      >
        {label}
        {children}
      </CustomButton>
    </>
  );
};

CMAnimateButton.propTypes = {
  label: PropTypes.string,
  children: PropTypes.any,
  color: PropTypes.string.isRequired,
};
export default CMAnimateButton;
