import React from "react";

import AddIcon from "@mui/icons-material/Add";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";

import { map } from "lodash";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  Autoplay,
  EffectCreative,
  Keyboard,
  Navigation,
  Pagination,
} from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";

import sliderBg1 from "../../assets/images/banner/slider-bg-1.jpg";
import sliderBg2 from "../../assets/images/banner/slider-bg-2.jpg";
import sliderBg3 from "../../assets/images/banner/slider-bg-3.jpg";
import CMAnimateButton from "../../components/ui-components/buttons/CMAnimateButton";

const Banner = () => {
  const footerHeight = useSelector((state) => state.common.footerHeight);
  const navigate = useNavigate();
  const content = [
    {
      image: sliderBg1,
      subtitle: "We are here for your care",
      title: "Best care & better doctor",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
      buttonLabel: "MAKE APPOINTMENT",
      path: "/appointment",
    },
    {
      image: sliderBg2,
      subtitle: "We are here for your care",
      title: "Best care & better doctor",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
      buttonLabel: "MAKE APPOINTMENT",
      path: "/appointment",
    },
    {
      image: sliderBg3,
      subtitle: "We are here for your care",
      title: "Best care & better doctor",
      description:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
      buttonLabel: "MAKE APPOINTMENT",
      path: "/appointment",
    },
  ];
  return (
    <>
      <Box component="section" className="banner-section">
        <Swiper
          slidesPerView={1}
          centeredSlides={true}
          effect={"creative"}
          creativeEffect={{
            prev: {
              shadow: true,
              translate: [0, 0, -400],
            },
            next: {
              translate: ["100%", 0, 0],
            },
          }}
          longSwipes={true}
          speed={600}
          preloadImages={true}
          loop={true}
          grabCursor={true}
          pagination={{
            clickable: true,
            dynamicBullets: true,
          }}
          autoplay={{
            delay: 2500,
            disableOnInteraction: false,
          }}
          keyboard={true}
          navigation={true}
          modules={[Autoplay, EffectCreative, Pagination, Navigation, Keyboard]}
          style={{
            "--swiper-pagination-color": "#0C0C0C",
          }}
          className="banner-slider"
        >
          {map(content, (item, index) => (
            <SwiperSlide key={index}>
              <Box className="main-wrapper-box position-relative">
                <Box
                  component="img"
                  src={item.image}
                  alt="slider-img"
                  sx={{ minHeight: `calc(100vh - ${footerHeight}px)` }}
                  className="img-fluid"
                />
                <Grid
                  container
                  justifyContent="center"
                  className="position-absolute center-left px-4"
                >
                  <Grid item xs={12} sm={10} md={7} lg={6}>
                    <Typography variant="h4" className="mb-3">
                      {item.subtitle}
                    </Typography>
                    <Typography variant="h1" className="mb-3">
                      {item.title}
                    </Typography>
                    <Typography variant="body1" className="mb-4">
                      {item.description}
                    </Typography>
                    <CMAnimateButton
                      startIcon={<AddIcon />}
                      color="primary"
                      onClick={() => navigate("/appointment")}
                      className="text-uppercase"
                    >
                      {item.buttonLabel}
                    </CMAnimateButton>
                  </Grid>
                  <Grid item xs={0} md={2}></Grid>
                </Grid>
              </Box>
            </SwiperSlide>
          ))}
        </Swiper>
      </Box>
    </>
  );
};

export default Banner;
